/* ------------------ */
/* Home Page Styles */
/* ----------------- */



// Article Styles
article.postify {
  overflow: hidden;
  background-color: $white;
  .post-content {
    position: relative;
    color: $cerulean;
    padding: 20px;
    margin: 20px;
    border-radius: 4px;
    max-width: 500px; } }

article.postify.has-thumbnail {
  .post-thumbnail {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    img {
      height: 100%;
      width: 100%;
      // -webkit-filter: brightness(50%)
      -webkit-transform: scale(1.2, 1.2);
      object-fit: cover; }
    .overlay {
      background-color: #000000;
      opacity: .4;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 2; } }
  .post-content {
    z-index: 3; } }

.post-grid article.postify {
