  // &:hover
  //   img
  //     filter: blur(0px)
  //     -webkit-filter: blur(0px)
 }  //     transition: all .2s

article.post-type-threesixty .post-content .post-author {
  display: none; }

/* ------------------ */
/* Single Page Styles */
/* ----------------- */
.single {
  background-color: $white;
  main {
    margin: auto;
    padding-top: 100px;
    h1.entry-title {
      @include subheadline-font;
      font-size: 40px;
      @include breakpoint($sm) {
        font-size: 60px; } }
    .alignright {
      float: right; }
    .alignleft {
      float: left; }
    img {
      margin: 1rem;
      max-width: 100%;
      width: 100% !important;
      height: auto !important;
      object-fit: contain; }
    table {
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
      overflow-y: scroll; } }
  header {
    margin-bottom: 20px; }
  article {
    max-width: 1100px;
    padding: 20px;
    @include breakpoint($sm) {
      padding: 50px; }
    padding-top: 40px;
    margin: auto; }



  .entry-social.ssk-left {
    bottom: 0;
    margin-bottom: 0px;
    @include breakpoint(769px) {
      top: 100px;
      display: block;
      a.ssk.ssk-text {
        margin-bottom: 5px; }
      .ssk.ssk-text::before {
        background-color: none !important; } } }
  .entry-social.ssk-bottom {
    display: block;
    @include breakpoint($sm) {
      display: none; } } }

.single article .tags {
  @include special-font;
  margin-bottom: 20px;
  a {
    display: inline-block;
    background-color: $brand-info;
    color: $white;
    padding: 5px 10px;
    &:hover {
      background-color: darken($brand-info, 10%); } } }


// .single article .entry-content
//   p:first-of-type
//     font-size: 20px
//   p:first-of-type:first-letter
//     font-size: 35px

.single article {
  .background {
    width: 100%;
    margin-top: -80px;
    max-height: 400px;
    overflow: hidden;
    top: 0;
    left: 0;
    // margin-bottom: 20px
    img {
      margin: 0px;
      object-fit: cover; } } }

.ssk-sticky {
  z-index: 300 !important; }

#author-box {
  background: $background;
  padding: 40px;
  margin-bottom: 0px;
  .author-photo {
    max-width: 200px;
    max-height: 200px;
    width: 200px;
    height: 200px;
    margin: auto;
    overflow: hidden;
    border-radius: 300px;
    img {
      object-fit: cover;
      margin: 0px;
      width: 100% !important;
      height: 100% !important;
 } }      // border-radius: 300px
  .author-info {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px; }
  .btn {
    width: 100%;
    max-width: 500px;
    display: block;
    margin: auto; } }

article blockquote.twitter-me {
  width: 100%;
  border-top: 2px solid $transp-black;
  border-bottom: 2px solid $transp-black;
  padding: 1.5rem;
  position: relative;
  background: transparent;
  border-left: none;
  a::before {
    font-family: "social-share-kit" !important;
    font-size: 22px;
    vertical-align: middle; }

  a p:last-of-type {
    margin-bottom: 0px; }
  a.ssk-twitter {
    display: inline;
    background-color: transparent; }
  a, a p {
    display: inline;
    opacity: .8;
    margin-bottom: 2px;
    font-size: 15px !important;
    padding: 6px; }
  a:before {
    color: $transp-black-2; }
  a.ssk-twitter:hover {
    background: $background; } }

article blockquote {
  background-color: $background;
  padding: 1rem;
  border-left: 7px solid $transp-black-2;
  p {
    font-size: 16px !important; }
  p:last-of-type {
    margin-bottom: 0px; } }

.entry-meta {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  color: $transp-black-5;

  time {
    @include special-font; }
  .divider {
    font-size: 8px; }
  .divider::after {
    content: "\f111";
    font-family: "FontAwesome";
    margin: auto;
    margin-right: 10px;
    margin-left: 10px;
    color: $transp-black;
    font-size: 8px;
    line-height: 8px; }
  .author {
    color: $transp-black-5;
    @include special-font;
    margin-bottom: 0px; } }


body.single-threesixty .entry-content {
  width: 100%; }

body.single-threesixty .content-wrap {
  padding-top: 0px; }

body.single-threesixty .main {
  padding-top: 20px; }
